import { createRouter, createWebHashHistory } from 'vue-router'
import App from "@/App.vue"
import HomePage from '@/views/HomePage.vue'
import RulesView from '@/views/RulesView.vue'
import BapCalculatorView from "@/views/BapCalculatorView.vue"
import CharacterBuilderView from '@/views/CharacterBuilderView.vue'
import WorldView from '@/views/WorldView.vue'
import AttachedTableView from '@/views/AttachedTableView.vue'
import BehindTheSceneView from '@/views/BehindTheSceneView.vue'
const routes = [
    {
        path: '/',
        name: 'App',
        component: App,
        meta: { title: 'CGC' },
        beforeEnter: (to, from, next) => {
            next('/home')
        }
    },
    {
        path: '/home',
        name: 'Home',
        component: HomePage,
        meta: { title: 'CGC主页' },
    },
    {
        path: '/world_view',
        name: 'WorldView',
        component: WorldView,
        meta: { title: 'CGC世界观简章' },
    },
    {
        path: '/rules',
        name: 'Rules',
        component: RulesView,
        meta: { title: 'CGC规则文档' },
    },
    {
        path: '/character_builder',
        name: 'CharacterBuilder',
        component: CharacterBuilderView,
        meta: { title: 'CGC角色卡构筑器' },
    },
    {
        path: '/bap_calculator',
        name: 'BapCalculator',
        component: BapCalculatorView,
        meta: { title: 'CGC战斗力计算器' },
    },
    {
        path: '/attached_table',
        name: 'AttachedTable',
        component: AttachedTableView,
        meta: { title: 'CGC附表' },
    },
    {
        path: '/behind_the_scene',
        name: 'BehindTheScene',
        component: BehindTheSceneView,
        meta: { title: '幕前幕后' },
    },
]
const router = createRouter({
    history: createWebHashHistory(),
    routes,
})
router.beforeEach((to, from, next) => {
    if (to.meta.title) {
        document.title = to.meta.title;
    }
    next()
})
export default router