import { versionTemplate } from "@/data/versionTemplate"

const getVersionInitialisation = (version) => {
    if (!version) { return getVersionInitialisation(versionTemplate.currentVersion) }
    const versionJson = versionTemplate[version]
    if (!(versionJson.base)) { return versionJson.fields }
    const lastVersionFields = getVersionInitialisation(versionJson.base)
    const mergedVersionFields = {
        ...lastVersionFields,
        ...versionJson.fields,
    }
    const resultFields = {}
    Object.entries(mergedVersionFields).forEach(([key, value]) => {
        if (key in versionJson.changedKeys) {
            resultFields[(versionJson.changedKeys)[key]] = value
        } else if (!(versionJson.unFields.includes(key))) {
            resultFields[key] = value
        }
    })
    return resultFields
}

export { getVersionInitialisation }