const validNonnegativeInteger = (param) => {
    if (Number.isInteger(param)) {
        if (param >= 0) return true
        else return false
    } else return false
}

const validPositiveInteger = (param) => {
    if (Number.isInteger(param)) {
        if (param > 0) return true
        else return false
    } else return false
}

export { validNonnegativeInteger, validPositiveInteger }