<template>
    <windowAlert :isMobile="isMobile" />
    <div v-if="webStatusStore.currentCharacter && ((!isMobile) || (isValidNavigator && blobDownloadable))"
        class="h-full w-full flex flex-col bg-[rgb(230,230,180)]">
        <mouse-tip :style="{
            top: vertical === 'top' ? 'auto' : `${mouseY}px`,
            bottom: vertical === 'top' ? `${windowHeight - mouseY}px` : 'auto',
            left: horizontal === 'left' ? 'auto' : `${mouseX}px`,
            right: horizontal === 'left' ? `${windowWidth - mouseX}px` : 'auto',
        }" />
        <!-- 顶部栏 -->
        <div class="w-full flex select-none" :style="{
            height: isMobile ? '20%' : '10%',
            flexWrap: isMobile ? 'wrap' : 'nowrap',
            flexDirection: isMobile ? 'column' : 'row',
        }">
            <div class="h-full" :style="{
                width: isMobile ? '16.66%' : '9.1%',
            }">
                <div class="top-box w-full h-1/2 flex justify-center items-center border border-[rgb(130,130,80)]"
                    :style="{
                        paddingTop: isMobile ? '4px' : '16px',
                        paddingBottom: isMobile ? '4px' : '16px',
                        fontSize: isMobile ? '10px' : '16px',
                    }">
                    {{ isMobile ? '属性点数' : '总属性点数' }}
                </div>
                <div class="middle-box w-full h-1/2 flex justify-center items-center border border-[rgb(130,130,80)]"
                    :style="{
                        color: webStatusStore.maxStatePoints >= webStatusStore.statePoints ? 'rgb(20,150,20)' : 'rgb(150,20,20)',
                        fontSize: isMobile ? '10px' : '16px',
                        paddingTop: isMobile ? '4px' : '16px',
                        paddingBottom: isMobile ? '4px' : '16px',
                    }">
                    {{ webStatusStore.statePoints }}/{{ webStatusStore.maxStatePoints }}
                </div>
            </div>
            <div v-for="(label, index) in topAbilityDimension" :key="index" class="flex flex-col" :style="{
                width: isMobile ? '22.23%' : '9.1%',
                height: isMobile ? '33.3%' : '100%',
            }">
                <div class="top-box w-full h-1/2 flex justify-center items-center border border-[rgb(130,130,80)]"
                    :style="{
                        paddingTop: isMobile ? '4px' : '16px',
                        paddingBottom: isMobile ? '4px' : '16px',
                        fontSize: isMobile ? '10px' : '16px',
                    }">
                    {{ isMobile ? `${label}点数` : `${label}技能点数` }}
                </div>
                <div class="middle-box w-full h-1/2 flex justify-center items-center border border-[rgb(130,130,80)]"
                    :style="{
                        color: webStatusStore.abilityPoints(label) >= webStatusStore.usedStateAbilityPoints(label) ? 'rgb(20,150,20)' : 'rgb(150,20,20)',
                        fontSize: isMobile ? '10px' : '16px',
                        paddingTop: isMobile ? '4px' : '16px',
                        paddingBottom: isMobile ? '4px' : '16px',
                    }">
                    {{ webStatusStore.usedStateAbilityPoints(label) }}/{{ webStatusStore.abilityPoints(label) }}
                </div>
            </div>
            <div :style="{
                width: isMobile ? '22.23%' : '9.1%',
                height: isMobile ? '33.33%' : '100%',
            }">
                <div class="top-box w-full h-1/2 flex justify-center items-center border border-[rgb(130,130,80)]"
                    :style="{
                        paddingTop: isMobile ? '4px' : '16px',
                        paddingBottom: isMobile ? '4px' : '16px',
                        fontSize: isMobile ? '10px' : '16px',
                    }">
                    {{ isMobile ? '通用点数' : '通用技能点数' }}
                </div>
                <div class="middle-box w-full h-1/2 flex justify-center items-center border border-[rgb(130,130,80)]"
                    :style="{
                        color: webStatusStore.generalStatePoint >= webStatusStore.usedStateAbilityPoints('通用') ? 'rgb(20,150,20)' : 'rgb(150,20,20)',
                        fontSize: isMobile ? '10px' : '16px',
                        paddingTop: isMobile ? '4px' : '16px',
                        paddingBottom: isMobile ? '4px' : '16px',
                    }">
                    {{ webStatusStore.usedStateAbilityPoints('通用') }}/{{ webStatusStore.generalStatePoint }}
                </div>
            </div>
            <div :style="{
                width: isMobile ? '16.65%' : '9%',
                height: '100%',
            }">
                <div class="top-box w-full h-1/2 flex justify-center items-center border border-[rgb(130,130,80)]"
                    :style="{
                        paddingTop: isMobile ? '4px' : '16px',
                        paddingBottom: isMobile ? '4px' : '16px',
                        fontSize: isMobile ? '10px' : '16px',
                    }">
                    领悟力
                </div>
                <div class="middle-box w-full h-1/2 flex justify-center items-center border border-[rgb(130,130,80)]"
                    :style="{
                        color: webStatusStore.level * 3 + 5 +
                            (webStatusStore.currentCharacter)['领悟力'] >= webStatusStore.comprehensionPoints ? 'rgb(20,150,20)' : 'rgb(150,20,20)',
                        fontSize: isMobile ? '10px' : '16px',
                        paddingTop: isMobile ? '4px' : '16px',
                        paddingBottom: isMobile ? '4px' : '16px',
                    }">
                    {{ webStatusStore.comprehensionPoints }}/{{ webStatusStore.level * 3 + 5 +
                        (webStatusStore.currentCharacter)['领悟力'] }}
                </div>
            </div>
        </div>
        <!-- 中间 -->
        <div ref="midBoxRef" class="w-full flex flex-col justify-start items-center overflow-y-auto" :style="{
            height: isMobile ? '70%' : '85%',
        }">
            <component v-if="childPage" :is="childPage" :isMobile="isMobile" />
        </div>
        <!-- 底部栏 -->
        <div class="w-full flex select-none" :style="{
            height: isMobile ? '10%' : '5%',
            fontSize: isMobile ? '12px' : '16px',
            flexDirection: isMobile ? 'column' : 'row',
            flexWrap: isMobile ? 'wrap' : 'nowrap',
        }">
            <div v-for="(label, index) in bottomPageDimension" :key="index"
                class="bottom-box flex flex-col cursor-pointer" @click="() => { setPage(label) }" :style="{
                    backgroundImage: activePage === index + 1 ?
                        'linear-gradient(to bottom, rgba(100,100,60,0.25), rgba(255,255,200,1.0))' : 'linear-gradient(to bottom, rgba(255,255,200,1.0), rgba(100,100,60,0.25))',
                    fontWeight: activePage === index + 1 ? 'bold' : 'normal',
                    color: activePage === index + 1 ? 'rgb(60,60,60)' : 'black',
                    width: isMobile ? '25%' : '14.3%',
                    height: isMobile ? (label === '版本更新信息' ? '100%' : '50%') : '100%',
                }">
                <div class="w-full h-full flex justify-center items-center border border-[rgb(130,130,80)]" :style="{
                    paddingTop: isMobile ? '4px' : '16px',
                    paddingBottom: isMobile ? '4px' : '16px',
                }">
                    {{ label }}
                </div>
            </div>
        </div>
    </div>
    <MobileWarningView v-else-if="isValidNavigator === false" warningType="in tencent app" />
    <MobileWarningView v-else-if="blobDownloadable === false" warningType="blob cannot download" />
    <div v-else></div>
</template>
<script setup>
import { useWebStatusStore } from '@/stores/webStatusStore'
import { onMounted, shallowRef, ref, onBeforeUnmount } from 'vue'
import CBBasicInfo from './character builder/CBBasicInfo.vue'
import CBCharacterStates from './character builder/CBCharacterStates.vue'
import CBCharacterAbilities from './character builder/CBCharacterAbilities.vue'
import CBCharacterSkills from './character builder/CBCharacterSkills.vue'
import CBItems from './character builder/CBItems.vue'
import CBInputAndOutput from './character builder/CBInputAndOutput.vue'
import CBVersionNotes from './character builder/CBVersionNotes.vue'
import CBIndex from './character builder/CBIndex.vue'
import mouseTip from '@/components/mouseTip.vue'
import windowAlert from '@/components/windowAlert.vue'
import { validMobileNavigator, checkBlobDownload } from '@/utils/validMobileNavigator'
import MobileWarningView from './MobileWarningView.vue'
const webStatusStore = useWebStatusStore()
const topAbilityDimension = ['力量', '体质', '灵巧', '意志', '智慧', '魔力', '魅力', '幸运']
const bottomPageDimension = ['基础信息', '角色属性', '角色技能', '天赋和魔法', '装备和背包', '导入和导出', '版本更新信息']
const activePage = ref(-1)
const childPage = shallowRef(CBIndex)
const midBoxRef = ref(null)
const setPage = (label) => {
    switch (label) {
        case '基础信息':
            childPage.value = CBBasicInfo
            activePage.value = 1
            break
        case '角色属性':
            childPage.value = CBCharacterStates
            activePage.value = 2
            break
        case '角色技能':
            childPage.value = CBCharacterAbilities
            activePage.value = 3
            break
        case '天赋和魔法':
            childPage.value = CBCharacterSkills
            activePage.value = 4
            break
        case '装备和背包':
            childPage.value = CBItems
            activePage.value = 5
            break
        case '导入和导出':
            childPage.value = CBInputAndOutput
            activePage.value = 6
            break
        case '版本更新信息':
            childPage.value = CBVersionNotes
            activePage.value = 7
            break
    }
    if (midBoxRef.value) {
        midBoxRef.value.scrollTop = 0
    }
}
const mouseX = ref(0)
const mouseY = ref(0)
const horizontal = ref('right')
const vertical = ref('top')
const updateMousePosition = (event) => {
    mouseX.value = event.clientX
    mouseY.value = event.clientY
    if (event.clientY < window.innerHeight * 0.2) { vertical.value = 'bottom' }
    else { vertical.value = 'top' }
    if (event.clientX > window.innerWidth * 0.8) { horizontal.value = 'left' }
    else { horizontal.value = 'right' }
}
const windowHeight = ref(window.innerHeight)
const windowWidth = ref(window.innerWidth)
const isMobile = ref(false)
const updateWindowSize = () => {
    windowHeight.value = window.innerHeight
    windowWidth.value = window.innerWidth
    isMobile.value = window.innerHeight > window.innerWidth
}
const isValidNavigator = ref(null)
const blobDownloadable = ref(null)
onMounted(() => {
    updateWindowSize()
    if (!(webStatusStore.currentCharacter)) {
        webStatusStore.setCharacter({})
    }
    window.addEventListener('mousemove', updateMousePosition)
    window.addEventListener('resize', updateWindowSize)
    isValidNavigator.value = validMobileNavigator()
    blobDownloadable.value = checkBlobDownload()
    if ((!isMobile.value) || (isValidNavigator.value && blobDownloadable.value)) {
        webStatusStore.setAlert('公告',
            [
                '技能的说明尚未完成，这部分的话，后续PL需要哪个就更新哪个，不会再计入更新日志。'
            ])
    }
})
onBeforeUnmount(() => {
    window.removeEventListener('mousemove', updateMousePosition)
    window.removeEventListener('resize', updateWindowSize)
})
</script>
<style scoped>
.top-box {
    background-image: linear-gradient(to bottom, rgba(255, 255, 200, 1.0), rgba(255, 255, 200, 0));
}

.middle-box {
    background-image: linear-gradient(to bottom, rgba(100, 100, 60, 0), rgba(100, 100, 60, 0.25));
}

.bottom-box:hover {
    background-color: rgba(100, 100, 60, 0.5);
}
</style>