<template>
    <div class="z-[3000] fixed top-0 left-0 w-full h-full bg-black bg-opacity-50 flex justify-center items-center">
        <div class="rounded-3xl bg-[rgb(220,220,160)] flex flex-col border-4 border-[rgb(160,160,100)] text-[rgb(60,60,60)] justify-center items-center"
            :style="{
                width: isMobile ? '90%' : '80%',
                maxHeight: isMobile ? '85%' : '80%',
            }">
            <div v-for="(line, index) in title" class="w-[90%] flex justify-center items-center font-bold" :key="index"
                :style="{
                    fontSize: isMobile ? '16px' : '24px',
                    paddingTop: isMobile ? '4px' : '8px',
                    paddingBottom: isMobile ? '4px' : '8px',
                }">
                {{ line }}</div>
            <div class="w-[90%] border-b-2 border-[rgb(200,200,150)]"></div>
            <div class="w-[90%] flex justify-center items-center pb-4 border-t-2 pt-4 border-[rgb(200,200,150)]">
                <div class="flex justify-center items-center" :style="{
                    fontSize: isMobile ? '20px' : '24px',
                }">
                    <input :style="{
                        width: isMobile ? '90%' : '100%',
                    }" v-model="thisInput" />
                </div>
            </div>
            <div
                class="w-[90%] flex flex-row justify-center items-center pb-4 border-t-2 pt-4 border-[rgb(200,200,150)]">
                <div class="click-button" :style="{
                    marginRight: isMobile ? '10px' : '16px',
                    paddingLeft: isMobile ? '10px' : '16px',
                    paddingRight: isMobile ? '10px' : '16px',
                    paddingTop: isMobile ? '4px' : '8px',
                    paddingBottom: isMobile ? '4px' : '8px',
                    fontSize: isMobile ? '20px' : '24px',
                }" @click="() => {
                    if (thisInput === correctInput) {
                        onConfirm()
                    } else {
                        onWarn()
                    }
                }">
                    确认
                </div>
                <div class="click-button" :style="{
                    marginLeft: isMobile ? '10px' : '16px',
                    paddingLeft: isMobile ? '10px' : '16px',
                    paddingRight: isMobile ? '10px' : '16px',
                    paddingTop: isMobile ? '4px' : '8px',
                    paddingBottom: isMobile ? '4px' : '8px',
                    fontSize: isMobile ? '20px' : '24px',
                }" @click="() => {
                    onCancel()
                }">
                    取消
                </div>
            </div>
        </div>
    </div>
</template>
<script setup>
import { defineProps, ref } from 'vue'
defineProps({
    isMobile: {
        type: Boolean,
        default: false,
    },
    title: {
        type: Array,
        required: true,
    },
    correctInput: {
        type: String,
        required: true,
    },
    onConfirm: {
        type: Function,
        required: true,
    },
    onWarn: {
        type: Function,
        required: true,
    },
    onCancel: {
        type: Function,
        required: true,
    },
})
const thisInput = ref('')
</script>
<style scoped>
.click-button {
    background-image: linear-gradient(to bottom, rgba(255, 255, 200, 0.25), rgba(180, 180, 130, 1.0));
    border: 1px solid rgb(130, 130, 80);
    border-radius: 8px;
    cursor: pointer;
    user-select: none;
}

.click-button:active {
    background-image: linear-gradient(to bottom, rgba(180, 180, 130, 1.0), rgba(255, 255, 200, 0.25));
}

input,
select,
textarea:focus {
    outline: none;
}

input,
select,
textarea {
    border: 2px solid rgb(130, 130, 80);
    background-color: rgb(255, 255, 200);
    border-radius: 4px;
    padding: 4px;
}

input:disabled {
    background-color: transparent;
    border: none;
}
</style>