import { createApp } from 'vue'
import { createPinia } from 'pinia'
import App from './App.vue'
import router from './router'
import VueLazyload from 'vue-lazyload'

createApp(App)
    .use(createPinia())
    .use(router)
    .use(VueLazyload, {
        preLoad: 1,
        error: 'error.png', // 加载失败时显示的图片
        loading: 'loading.gif', // 加载时显示的占位图
        attempt: 1,
    })
    .mount('#app')
