const validMobileNavigator = () => {
    const validInTencent = () => {
        const userAgent = navigator.userAgent.toLowerCase()
        if (/qq|micromessenger/.test(userAgent)) {
            return false
        } else { return true }
    }
    return validInTencent()
}

const supportNavigatorShare = () => {
    return !!navigator.share
}

const isMobile = () => {
    const validInTencent = () => {
        const userAgent = navigator.userAgent.toLowerCase()
        if (/iphone|ipad|mac|android/.test(userAgent)) {
            return true
        } else { return false }
    }
    return validInTencent()
}

const checkBlobDownload = () => {
    const isBlobSupported = !!window.Blob && typeof Blob === 'function'
    const a = document.createElement('a')
    const isDownloadSupported = typeof a.download !== 'undefined'
    return isBlobSupported && isDownloadSupported
}

export { validMobileNavigator, isMobile, supportNavigatorShare, checkBlobDownload }