<template>
    <windowAlert :isMobile="isMobile" />
    <div class="h-full w-full flex flex-col bg-[rgb(230,230,180)] select-none break-all">
        <!-- 顶部栏 -->
        <div class="h-[7.5%] w-full flex flex-row">
            <div v-for="(label, index) in worldViewDimension" :key="index"
                class="bottom-box w-1/5 flex flex-col cursor-pointer" @click="() => { setPage(label) }" :style="{
                    backgroundImage: activePage === index + 1 ?
                        'linear-gradient(to bottom, rgba(100,100,60,0.25), rgba(255,255,200,1.0))' : 'linear-gradient(to bottom, rgba(255,255,200,1.0), rgba(100,100,60,0.25))',
                    fontWeight: activePage === index + 1 ? 'bold' : 'normal',
                    color: activePage === index + 1 ? 'rgb(60,60,60)' : 'black',
                }">
                <div class="w-full h-full flex justify-center items-center pt-4 pb-4 border border-[rgb(130,130,80)]"
                    :style="{
                        fontSize: isMobile ? '14px' : '20px',
                    }">
                    {{ label }}
                </div>
            </div>
        </div>
        <!-- 内容 -->
        <div class="h-[85%] w-full flex justify-center items-center">
            <div
                class="h-[95%] w-[95%] border-4 border-[rgb(130,130,80)] rounded-3xl bg-[rgb(200,200,150)] flex flex-row items-center">
                <!-- 左侧选项 -->
                <div ref="labelRef" class="h-[95%] flex justify-center items-center overflow-y-auto" :style="{
                    width: isMobile ? '35%' : '20%',
                }">
                    <div class="h-full flex flex-col" :style="{
                        width: isMobile ? '95%' : '83.3%',
                    }">
                        <div v-for="(label, index) in worldViewTitleDimension" :key="index"
                            class="w-full pt-2 pb-2 flex justify-center items-center">
                            <div class="click-button cursor-pointer w-full" @click="() => setContent(label)" :style="{
                                backgroundImage: activeContent === label ?
                                    'linear-gradient(to bottom, rgba(180, 180, 130, 1.0), rgba(255, 255, 200, 0.5))' :
                                    'linear-gradient(to bottom, rgba(255, 255, 200, 0.5), rgba(180, 180, 130, 1.0))',
                                paddingTop: isMobile ? '4px' : '8px',
                                paddingBottom: isMobile ? '4px' : '8px',
                                fontSize: isMobile ? '12px' : '16px',
                            }">
                                {{ label }}
                            </div>
                        </div>
                    </div>
                </div>
                <!-- 右侧说明 -->
                <div class="h-[95%] flex flex-row justify-center items-center border-l border-[rgb(130,130,80)]" :style="{
                    width: isMobile ? '65%' : '80%',
                }">
                    <div
                        class="w-[95%] h-full flex justify-center items-center flex-col border-2 border-[rgb(130,130,80)] rounded bg-[rgba(230,230,180,0.5)]">
                        <!-- 标题 -->
                        <div class="w-[95%] h-[12.5%] flex justify-center items-center border-b border-[rgb(130,130,80)] text-[rgb(60,60,60)] font-bold"
                            :style="{
                                fontSize: isMobile ? '20px' : '24px',
                            }">
                            {{ activeContent }}
                        </div>
                        <!-- 内容 -->
                        <div ref="contentRef" class="w-[95%] h-[87.5%] overflow-y-auto flex flex-col items-center">
                            <WorldViewBasic :subjectLabel="subjectLabel" :instructionKey="activeContent"
                                :isMobile="isMobile" :changeMusic="changeMusic" :playMusic="playMusic"
                                :setMusic="setMusic" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- 底部音乐栏 -->
        <div class="h-[7.5%] w-full flex flex-row" :style="{
            backgroundImage: 'linear-gradient(to bottom, rgba(255,255,200,1.0), rgba(100,100,60,0.25))',
        }">
            <div class="h-full flex justify-center items-center" :style="{
                width: isMobile ? '14%' : '8%',
            }">
                <div class="w-full h-[65%] flex justify-center items-centers">
                    <img class="h-full aspect-square rounded-[50%] cursor-pointer"
                        :src="isAudioPlaying ? 'system/Pause.png' : 'system/Play.png'" @click="playMusic" />
                </div>
            </div>
            <div class="h-full flex justify-evenly items-center" :style="{
                flexDirection: isMobile ? 'column' : 'row',
                width: isMobile ? '28%' : '18%',
            }">
                <span class="font-bold text-[rgb(60,60,60)]" :style="{
                    fontSize: isMobile ? '14px' : '20px',
                }">{{ audioName }}</span>
                <span :style="{
                    fontSize: isMobile ? '12px' : '16px',
                }">{{ audioType }}</span>
            </div>
            <div class="h-full flex justify-center items-center" :style="{
                width: isMobile ? '58%' : '74%',
            }">
                <input v-if="audioDuration !== 0" type="range" min="0" :max="audioDuration" v-model="audioCurrentTime"
                    @input="seekAudio" :style="{
                        width: isMobile ? '90%' : '90%',
                    }" />
            </div>
        </div>
        <audio ref="audioRef" @timeupdate="handleTimeUpdate">
            <source :src="audioUrl" type="audio/mp3">
        </audio>
    </div>
</template>
<script setup>
import { onMounted, ref, onBeforeUnmount } from 'vue'
import WorldViewBasic from './world view/WorldViewBasic.vue'
import { useWebStatusStore } from '@/stores/webStatusStore'
import windowAlert from '@/components/windowAlert.vue'
const webStatusStore = useWebStatusStore()
const audioRef = ref(null)
const audioUrl = ref('')
const audioName = ref('')
const audioType = ref('')
const isAudioPlaying = ref(false)
const playMusic = () => {
    if (audioUrl.value === '') {
        webStatusStore.setAlert('未找到音乐',
            [
                '播放器没有找到任何音乐。',
                '点击一个内容下的音符按钮，播放器会自动加载并进行播放。',
            ])
        return
    }
    if (audioRef.value) {
        if (!isAudioPlaying.value) {
            audioRef.value.play()
            isAudioPlaying.value = true
        } else {
            audioRef.value.pause()
            isAudioPlaying.value = false
        }
    }
}
const changeMusic = (music) => {
    if (audioRef.value) {
        audioUrl.value = music['url']
        audioName.value = music['name']
        audioType.value = music['type']
        audioRef.value.load()
        audioRef.value.play()
        isAudioPlaying.value = true
    }
}
const setMusic = (music) => {
    if (audioRef.value) {
        audioUrl.value = music['url']
        audioName.value = music['name']
        audioType.value = music['type']
        audioRef.value.load()
    }
}
const audioCurrentTime = ref(0)
const audioDuration = ref(0)
const seekAudio = () => {
    if (audioRef.value) {
        audioRef.value.currentTime = audioCurrentTime.value
    }
}
const handleTimeUpdate = (event) => {
    audioCurrentTime.value = event.target.currentTime
    audioDuration.value = event.target.duration
    if (audioCurrentTime.value === audioDuration.value && isAudioPlaying.value) {
        audioRef.value.pause()
        isAudioPlaying.value = false
        audioCurrentTime.value = 0
    }
}
const labelRef = ref(null)
const contentRef = ref(null)
const activePage = ref(-1)
const worldViewDimension = ['世界地理', '种族一览', '部分派系', '部分角色', '部分奇观']
const worldViewTitleDimension = ref([])
const setPage = (label) => {
    subjectLabel.value = label
    switch (label) {
        case '世界地理':
            activePage.value = 1
            worldViewTitleDimension.value = ['赛普顿群岛', '法纳斯平原', '中土平原', '九州山脉', '波札那沙漠',
                '阿拉赫比特平原', '稻荷森林', '黑风沙漠', '岩硫火山岛', '极寒之地',
                '地心界', '海天界', '云山界']
            activeContent.value = '赛普顿群岛'
            break
        case '种族一览':
            activePage.value = 2
            worldViewTitleDimension.value = ['人类', '鬼', '半兽人', '花妖精', '史莱姆人', '水母人',
                '羽蜥人', '小精灵', '地龙', '海蛇人', '人鱼', '天使']
            activeContent.value = '人类'
            break
        case '部分派系':
            activePage.value = 3
            worldViewTitleDimension.value = ['圣堂', '乌诺海盗团', '朝廷', '姮娥宫', '黑玫瑰义军', '太阳王朝',
                '流沙军团', '大漠反抗军', '部落联合军', '稻荷联盟', '巨树贤者会', '大地行者会',
                '熔火王庭', '星瞳峰', '征服者军团', '七峰塔', '十二星宫']
            activeContent.value = '圣堂'
            break
        case '部分角色':
            activePage.value = 4
            worldViewTitleDimension.value = ['普莉茜菈', '银莲', '阿萨姆', '白鹤', '花雕', '韩默默',
                '可可', '卡蕾娜', '娅菈', '托蒙塔尔', '塞蕾妮贝菈',
                '露塔莎', '莉瑟', '阿星']
            activeContent.value = '普莉茜菈'
            break
        case '部分奇观':
            activePage.value = 5
            worldViewTitleDimension.value = ['龙绡遗迹', '七峰之塔', '树城', '星云城']
            activeContent.value = '龙绡遗迹'
            break
    }
    if (labelRef.value) {
        labelRef.value.scrollTop = 0
    }
    if (contentRef.value) {
        contentRef.value.scrollTop = 0
    }
}
const subjectLabel = ref('')
const activeContent = ref('')
const setContent = (label) => {
    activeContent.value = label
    if (contentRef.value) {
        contentRef.value.scrollTop = 0
    }
}
const isMobile = ref(false)
const updateSize = () => {
    const windowHeight = window.innerHeight
    const windowWidth = window.innerWidth
    if (windowHeight > windowWidth) {
        isMobile.value = true
    } else {
        isMobile.value = false
    }
}
onMounted(() => {
    window.addEventListener("resize", updateSize)
    updateSize()
    setPage('世界地理')
})
onBeforeUnmount(() => {
    window.removeEventListener("resize", updateSize)
})
</script>
<style scoped>
.click-button {
    border: 2px solid rgb(130, 130, 80);
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    font-weight: bold;
    color: rgb(60, 60, 60);
}

.click-button:hover {
    background-color: rgba(100, 100, 60, 0.5);
}
</style>