<template>
    <div class="h-full w-full flex flex-col bg-[rgb(230,230,180)]">
        <!-- 顶部栏 -->
        <div class="w-full flex flex-row select-none" :style="{
            height: isMobile ? '7.5%' : '10%',
        }">
            <div v-for="(label, index) in rulesDimension" :key="index"
                class="bottom-box w-1/3 flex flex-col cursor-pointer" @click="() => { setPage(label) }" :style="{
                    backgroundImage: activePage === index + 1 ?
                        'linear-gradient(to bottom, rgba(100,100,60,0.25), rgba(255,255,200,1.0))' : 'linear-gradient(to bottom, rgba(255,255,200,1.0), rgba(100,100,60,0.25))',
                    fontWeight: activePage === index + 1 ? 'bold' : 'normal',
                    color: activePage === index + 1 ? 'rgb(60,60,60)' : 'black',
                }">
                <div class="w-full h-full flex justify-center items-center pt-4 pb-4 border border-[rgb(130,130,80)]"
                    :style="{
                        fontSize: isMobile ? '14px' : '20px',
                    }">
                    {{ label }}
                </div>
            </div>
        </div>
        <!-- 内容 -->
        <div ref="midBoxRef" class="overflow-y-auto w-full flex flex-row select-none justify-center" :style="{
            height: isMobile ? '92.5%' : '90%',
        }">
            <RulesBasic v-if="childPageKeyword" :keyword="childPageKeyword" :isMobile="isMobile" />
        </div>
    </div>
</template>
<script setup>
import { onMounted, ref, onBeforeUnmount } from 'vue'
import RulesBasic from './rules/RulesBasic.vue'
const rulesDimension = ['角色扮演规则', '整体游戏规则', '战斗轮规则']
const activePage = ref(-1)
const childPageKeyword = ref('')
const midBoxRef = ref(null)
const setPage = (label) => {
    childPageKeyword.value = label
    switch (label) {
        case '角色扮演规则':
            activePage.value = 1
            break
        case '整体游戏规则':
            activePage.value = 2
            break
        case '战斗轮规则':
            activePage.value = 3
            break
    }
    if (midBoxRef.value) {
        midBoxRef.value.scrollTop = 0
    }
}
const isMobile = ref(false)
const updateWindowSize = () => {
    isMobile.value = window.innerHeight > window.innerWidth
}
onMounted(() => {
    updateWindowSize()
    window.addEventListener('resize', updateWindowSize)
    setPage('角色扮演规则')
})
onBeforeUnmount(() => {
    window.removeEventListener('resize', updateWindowSize)
})
</script>
<style></style>