<template>
    <div class="w-full h-full bg-[rgb(230,230,180)] flex flex-col overflow-y-auto">
        <!-- 输入属性 -->
        <div class="w-full flex" :style="{
            flexDirection: isMobile ? 'column' : 'row',
        }">
            <div class="flex flex-col justify-center items-center" :style="{
                width: isMobile ? '100%' : '33.3%',
            }">
                <div class="inputbar">
                    健康值：
                    <input @change="calculateFinal" v-model="inputHp" type="number" />
                </div>
                <div class="inputbar">
                    防御力：
                    <input @change="calculateFinal" v-model="inputDfd" type="number" />
                </div>
                <div class="inputbar">
                    暴击值：
                    <input @change="calculateFinal" v-model="inputCri" type="number" />
                </div>
                <div class="inputbar">
                    生存分数：{{ scoreSur }}
                </div>
            </div>
            <div class="flex flex-col justify-center items-center" :style="{
                width: isMobile ? '100%' : '33.3%',
            }">
                <div class="inputbar">
                    伤害值：
                    <input @change="calculateFinal" v-model="inputDmg" type="number" />
                </div>
                <div class="inputbar">
                    攻击力：
                    <input @change="calculateFinal" v-model="inputAtk" type="number" />
                </div>
                <div class="inputbar">
                    士气值：
                    <input @change="calculateFinal" v-model="inputMor" type="number" />
                </div>
                <div class="inputbar">
                    威胁分数：{{ scoreThr }}
                </div>
            </div>
            <div class="flex flex-col justify-center items-center" :style="{
                width: isMobile ? '100%' : '33.3%',
            }">
                <div class="inputbar">
                    魔法值：
                    <input @change="calculateFinal" v-model="inputMp" type="number" />
                </div>
                <div class="inputbar">
                    移动力：
                    <input @change="calculateFinal" v-model="inputMov" type="number" />
                </div>
                <div class="inputbar">
                    先攻值：
                    <input @change="calculateFinal" v-model="inputSpd" type="number" />
                </div>
                <div class="inputbar">
                    灵活分数：{{ scorePor }}
                </div>
            </div>
        </div>
        <!-- 总分 -->
        <div class="w-full flex" :style="{
            flexDirection: isMobile ? 'column' : 'row',
            paddingTop: isMobile ? '40px' : '80px',
        }">
            <div class="flex justify-center items-center" :style="{
                width: isMobile ? '100%' : '50%',
            }">
                <div class="inputbar">
                    等级：
                    <input @change="calculateFinal" v-model="inputLevel" type="number" />
                </div>
            </div>
            <div class="flex justify-center items-center" :style="{
                width: isMobile ? '100%' : '50%',
            }">总战斗力得分：{{ scoreSur + scoreThr + scorePor }}
            </div>
        </div>
        <!-- 参考 -->
        <div class="w-full flex justify-center items-center" :style="{
            paddingTop: isMobile ? '40px' : '80px',
            paddingBottom: isMobile ? '20px' : '40px',
        }">
            <table class="w-4/5">
                <tbody>
                    <tr>
                        <th>参考角色</th>
                        <th>参考战斗力</th>
                    </tr>
                    <tr v-for="(obj, index) in Object.entries(references)" :key="index">
                        <td>
                            <div class="flex flex-row items-center" :style="isMobile ? {
                                fontSize: '12px',
                            } : {}">
                                <img class="h-[50px] pr-10 pl-10 rounded-[50%]" :src="obj[1][1]" atl="" :style="isMobile ? {
                                    paddingLeft: '4px',
                                    paddingRight: '4px',
                                } : {}" />
                                <div>{{ obj[0] }}</div>
                            </div>
                        </td>
                        <td>{{ obj[1][0] }}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>
<script setup>
import { onBeforeUnmount, onMounted, ref } from 'vue'
const references = {
    '柳雀（开局，至，大事件01结束前）': [14207, 'character icon/Liuque.png'],
    '柳雀（大事件01结束后，至，大事件02结束前）': [18100, 'character icon/Liuque.png'],
    '柳雀（大事件02结束后，至，终局前）': [25555, 'character icon/Liuque.png'],
    '银莲（开局，至，大事件01结束前，保底）': [26374, 'character icon/Yinlian.png'],
    '银莲（大事件01结束后，至，大事件02结束前，保底）': [33216, 'character icon/Yinlian.png'],
    '银莲（大事件02结束后，至，终局前，保底）': [43537, 'character icon/Yinlian.png'],
    '洛克（开局，至，大事件01结束前）': [17655, 'character icon/Lock.png'],
    '洛克（大事件01结束后，至，大事件02结束前）': [24425, 'character icon/Lock.png'],
    '洛克（大事件02结束后，至，终局前）': [38245, 'character icon/Lock.png'],
    '白鹤（书团《八方为剑》）': [10579, 'character icon/Tsuru.png'],
    '白鹤（书团《风之歌》）': [20224, 'character icon/Tsuru.png'],
    '白鹤（书团《珊石海遗章》）': [32171, 'character icon/Tsuru.png'],
    '白鹤（开局，至，大事件01结束前）': [45343, 'character icon/Tsuru.png'],
    '白鹤（大事件01结束后，至，大事件02结束前）': [49326, 'character icon/Tsuru.png'],
    '白鹤（大事件02结束后，至，终局前）': [53457, 'character icon/Tsuru.png'],
    '花雕（固定）': [268433, 'character icon/Huadiao.png'],
    // '塔克布娜·普兰蒂娅（固定）': [1445508, 'Takapunna.png'],
}
const inputHp = ref(0)
const inputDfd = ref(0)
const inputCri = ref(0)
const inputAtk = ref(0)
const inputDmg = ref(0)
const inputMor = ref(0)
const inputMp = ref(0)
const inputMov = ref(0)
const inputSpd = ref(0)
const inputLevel = ref(0)
const scoreSur = ref(0)
const scoreThr = ref(0)
const scorePor = ref(0)
const calculateBap = (value, label) => {
    switch (label) {
        case 'hp':
            return (Math.pow(value, 1.2) * 0.025)
        case 'mp':
            return (Math.pow(value, 1.3) * 0.25)
        case 'atk':
            return (Math.pow(value, 0.8) * 2.5)
        case 'dfd':
            return (Math.pow(value, 0.8) * 2.5)
        case 'dmg':
            return (Math.pow(value, 0.7) * 4.5)
        case 'mov':
            return (Math.pow(value, 1.5) / 125 + 1)
        case 'mor':
            return ((value > 100 ? 100 : value) * 0.0075 + 1)
        case 'cri':
            return (150 / (150 - (value > 100 ? 100 : value)))
        case 'spd':
            if (value <= 908) {
                return (1 / (1 + Math.exp(-0.005 * (value - 600))) * (value + 600) - 28.455) * 0.2
            } else {
                return (400 * Math.log(1 + 0.01 * (value - 800)) + 920.382) * 0.2
            }
        default:
            return 0
    }
}
const calculateFinal = () => {
    const surp = calculateBap(inputHp.value, 'hp') * calculateBap(inputDfd.value, 'dfd') * calculateBap(inputCri.value, 'cri')
    const thrp = calculateBap(inputDmg.value, 'dmg') * calculateBap(inputAtk.value, 'atk') * calculateBap(inputMor.value, 'mor')
    const porp = (Math.pow(calculateBap(inputMp.value, 'mp'), 2) + Math.pow(calculateBap(inputSpd.value, 'spd'), 2)) / 2 * calculateBap(inputMov.value, 'mov')
    const lf = 1 + inputLevel.value / 100
    scoreSur.value = Math.round(Math.pow(surp, 0.8) * lf)
    scoreThr.value = Math.round(Math.pow(thrp, 0.8) * lf)
    scorePor.value = Math.round(Math.pow(porp, 0.8) * lf)
}
const isMobile = ref(false)
const updateSize = () => {
    const windowHeight = window.innerHeight
    const windowWidth = window.innerWidth
    if (windowHeight > windowWidth) {
        isMobile.value = true
    } else {
        isMobile.value = false
    }
}
onMounted(() => {
    window.addEventListener("resize", updateSize)
    updateSize()
})
onBeforeUnmount(() => {
    window.removeEventListener("resize", updateSize)
})
</script>
<style scoped>
th {
    background-color: rgb(200, 200, 145);
}

th,
td {
    border: 1px solid rgb(130, 130, 80);
    padding: 10px;
}

input {
    padding: 5px;
    border: 1px solid rgb(130, 130, 80);
    background-color: rgb(255, 255, 200);
    border-radius: 10px;
    width: 60%;
}

input:focus {
    outline: none;
}

.inputbar {
    padding-top: 20px;
    padding-bottom: 20px;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>