<template>
    <ConfirmationAlert v-if="confirmationDisplay" :correctInput="confirmationInput" :title="confirmationTitle"
        :onCancel="() => {
            confirmationDisplay = false
            webStatusStore.setAlert('下定决心……',
                [
                    '准备好接受剧透再来吧。'
                ])
        }" :onConfirm="() => {
            if (currentStoryLabel) {
                story = currentStoryLabel
            }
            confirmationDisplay = false
        }" :isMobile="isMobile" :onWarn="() => {
            confirmationDisplay = false
            webStatusStore.setAlert('输入错误！',
                [
                    '也许你并不想要阅读？'
                ])
        }" />
    <windowAlert :isMobile="isMobile" />
    <div v-if="story === ''"
        class="h-full w-full flex flex-col justify-start items-center bg-[rgb(230,230,180)] overflow-y-auto">
        <div class="w-[95%] flex flex-col justify-start items-center select-none">
            <div v-for="([groupLabel, groupValues], index) in Object.entries(scenes)" class="container" :key="index"
                :style="{
                    paddingTop: isMobile ? '12px' : '24px',
                    paddingBottom: isMobile ? '12px' : '24px',
                    marginTop: isMobile ? '12px' : '24px',
                    marginBottom: isMobile ? '12px' : '24px',
                }">
                <div class="outer-box">
                    <div class="inner-box" :style="{
                        paddingTop: isMobile ? '8px' : '16px',
                        paddingBottom: isMobile ? '8px' : '16px',
                    }">
                        <span :style="{
                            fontSize: isMobile ? '16px' : '20px',
                        }">{{ groupLabel }}</span>
                    </div>
                    <div class="inner-box">
                        <div v-for="(storyName, index2) in groupValues" class="text-center click-button p-2 m-2"
                            :key="index + '-' + index2" :style="{
                                width: isMobile ? '60%' : '20%',
                                fontSize: isMobile ? '14px' : '16px',
                            }" @click="() => {
                                confirmationInput = '我要阅读：' + storyName
                                confirmationTitle = [
                                    '该故事属于幕后故事，会涉及到一部分不对玩家公开的剧透信息！',
                                    `请再三确认你要阅读该故事，并输入“我要阅读：${storyName}”！`,
                                    '然后点击下方的确定按钮进行阅读！',
                                ]
                                currentStoryLabel = storyName
                                confirmationDisplay = true
                            }">
                            {{ storyName }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <StoryReader v-else :story="story" :clearStory="() => { story = '' }" :isMobile="isMobile" />
</template>
<script setup>
import { ref, onMounted, onBeforeUnmount } from 'vue'
import ConfirmationAlert from '@/components/ConfirmationAlert.vue'
import windowAlert from '@/components/windowAlert.vue'
import { useWebStatusStore } from '@/stores/webStatusStore'
import StoryReader from './behind the scene/StoryReader.vue'
const webStatusStore = useWebStatusStore()
const scenes = {
    // '覆雪葬歌': [
    //     '艾达与老师',
    // ],
    // '余烬悲鸣': [
    //     '工作交代',
    //     '塔克的复盘',
    // ],
    // '吃人的妖怪': [
    //     '查抄酒厂',
    //     '山螭拜山门',
    //     '"妖怪"',
    // ],
    '黑色风暴与永恒黄沙': [
        '大联合部落建设（未写完）',
        // '关于澡堂',
        // '八剑的归处',
    ],
}
const confirmationInput = ref('')
const confirmationDisplay = ref(false)
const confirmationTitle = ref('')
const currentStoryLabel = ref(null)
const story = ref('')
const isMobile = ref(false)
const updateWindowSize = () => {
    isMobile.value = window.innerHeight > window.innerWidth
}
onMounted(() => {
    updateWindowSize()
    window.addEventListener('resize', updateWindowSize)
})
onBeforeUnmount(() => {
    window.removeEventListener('resize', updateWindowSize)
})
</script>
<style scoped>
.container {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: rgb(200, 200, 150);
    border-radius: 8px;
    border: 2px solid rgb(130, 130, 80);
}

.inner-box {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.outer-box {
    width: 100%;
    display: flex;
    flex-direction: column;
}

span {
    color: rgb(60, 60, 60);
    font-weight: bold;
}

.click-button {
    border: 2px solid rgb(130, 130, 80);
    border-radius: 8px;
    font-weight: bold;
    color: rgb(60, 60, 60);
    cursor: pointer;
    background-image: linear-gradient(to bottom, rgba(255, 255, 200, 0.5), rgba(180, 180, 130, 1.0));
}

.click-button:hover {
    background-color: rgba(100, 100, 60, 0.5);
}

.click-button:active {
    background-image: linear-gradient(to bottom, rgba(180, 180, 130, 1.0), rgba(255, 255, 200, 0.5));
}
</style>