import { defineStore } from 'pinia'
import { getVersionInitialisation } from '@/utils/stateInitialiser'
import { abilityData, getAbilityPercentage, getAbilityComprehensionCost } from '@/data/abilityData'
import { raceInstructions } from '@/data/instructions'

export const useWebStatusStore = defineStore('web_status', {
    state: () => ({
        currentCharacter: null,
        mouseTip: false,
        mouseTipContent: [],
        windowAlert: false,
        windowAlertTitle: '',
        windowAlertContent: [],
    }),
    getters: {
        getPoints: (state) => (label) => {
            let value = (state.currentCharacter)[label][0]
            if (!Number.isInteger(value)) { value = 0 }
            if (value < 0) { value = 0 }
            if (value <= 600) return value
            if (value <= 800) return value * 1.5 - 300
            if (value <= 1000) return value * 2 - 700
            return value * 3 - 1700
        },
        statePoints: (state) => {
            let pts = 0
            const allStates = ['力量', '体质', '灵巧', '意志', '智慧', '魔力', '魅力', '幸运']
            allStates.forEach(label => {
                pts += state.getPoints(label)
            })
            return pts
        },
        maxStatePoints: (state) => {
            if ((state.currentCharacter)['角色种族'] === '') { return 0 }
            else if ((state.currentCharacter)['角色种族'] === '其他') { return 4800 + (state.currentCharacter)['额外属性点数'] }
            else { return raceInstructions[(state.currentCharacter)['角色种族']]['属性点数'] + (state.currentCharacter)['额外属性点数'] }
        },
        generalStatePoint: (state) => {
            let pts = 0
            const allStates = ['力量', '体质', '灵巧', '意志', '智慧', '魔力', '魅力', '幸运']
            allStates.forEach(label => {
                pts += (state.abilityPoints(label) >= state.usedStateAbilityPoints(label) ? state.abilityPoints(label) - state.usedStateAbilityPoints(label) : 0)
            })
            pts += state.currentCharacter[`额外通用技能点数`]
            return pts
        },
        abilityPoints: (state) => (label) => {
            const value = state.currentCharacter[label][0] * 2 + state.currentCharacter[label][0] * state.level * 0.05
                + state.level * 0.5 + state.currentCharacter[`额外${label}技能点数`]
            return Math.floor((value).toFixed(3))
        },
        comprehensionPoints: (state) => {
            let pts = 0
            Object.keys(abilityData).forEach(key => {
                pts += (state.currentCharacter)[key][0] !== 0 ? getAbilityComprehensionCost(key) : 0
            });
            (state.currentCharacter)['天赋'].forEach(talent => {
                pts += talent[2]
            })
            return pts
        },
        usedStateAbilityPoints: (state) => (label) => {
            let pts = 0
            Object.keys(abilityData).forEach(key => {
                pts += Math.ceil(state.getPoints(key) * getAbilityPercentage(key, label))
                if (getAbilityPercentage(key, label) === null) { console.warn(`属性${key}的${label}百分比不存在！`) }
            })
            return pts
        },
        level: (state) => {
            if ((state.currentCharacter)['阅历'] < 0) return 0
            const base = (state.currentCharacter)['阅历'] * 0.2 + 0.25
            const result = Math.floor((Math.pow(base, 0.5) - 0.5).toFixed(3))
            return result > 500 ? 500 : result
        },
        health: (state) => {
            return Math.floor((state.currentCharacter)['体质'][0] * 2 + 500 + Math.pow(state.level, 0.75) * 10)
        },
        mana: (state) => {
            return Math.floor((state.currentCharacter)['魔力'][0] * 0.2 + state.level * 0.5)
        },
        damage: (state) => {
            return (state.currentCharacter)['力量'][0] * 0.12 + 40
        },
        attack: (state) => {
            return Math.max((state.currentCharacter)['力量'][0] * 0.08 + (state.currentCharacter)['魔力'][0] * 0.02 + state.level * 2, 40)
        },
        defend: (state) => {
            return Math.max((state.currentCharacter)['体质'][0] * 0.1 + state.level * 2, 40)
        },
        speed: (state) => {
            return Math.floor(Math.pow((state.currentCharacter)['灵巧'][0], 0.9) * Math.pow((state.currentCharacter)['意志'][0], 0.1))
        },
        morale: (state) => {
            return Math.floor((state.currentCharacter)['意志'][0] * 0.1 + (state.currentCharacter)['魅力'][0] * 0.25 + 25)
        },
        critic: (state) => {
            return Math.floor((state.currentCharacter)['力量'][0] * 0.1 + (state.currentCharacter)['灵巧'][0] * 0.05
                + (state.currentCharacter)['幸运'][0] * 0.04 + 5)
        },
        movement: (state) => {
            return Math.floor((state.currentCharacter)['灵巧'][0] * 0.01)
        },
        growth: (state) => {
            return Math.floor((state.currentCharacter)['智慧'][0] * 0.06 + 70)
        },
        healthRege: (state) => {
            const factor1 = Math.pow((state.currentCharacter)['体质'][0], 2) / ((state.currentCharacter)['体质'][0] + 5000)
            const factor2 = Math.pow((state.currentCharacter)['意志'][0], 2) / ((state.currentCharacter)['意志'][0] + 3000)
            const factor3 = (state.currentCharacter)['健康值'][0] === 0 ? 0 : (state.currentCharacter)['健康值'][1] / (state.currentCharacter)['健康值'][0]
            return Math.floor((factor1 + factor2) * (1 + factor3))
        },
        manaRege: (state) => {
            const factor1 = (state.currentCharacter)['魔力'][0] * 0.04
            const factor2 = (state.currentCharacter)['智慧'][0] * 0.02
            const factor3 = (state.currentCharacter)['魔法值'][0] === 0 ? 0 : (state.currentCharacter)['魔法值'][1] / (state.currentCharacter)['魔法值'][0]
            return Math.floor((factor1 + factor2) * (1 + factor3))
        },
        characterBuilderValidation: (state) => {
            // 0 = 红字错误，1 = 黄字警告，2 = 绿字通过
            const warningLog = []
            if ((state.currentCharacter)['所属玩家'] === '') {
                warningLog.push(['你需要填写你的玩家昵称！', 0])
            }
            if ((state.currentCharacter)['角色名字'] === '') {
                warningLog.push(['你需要填写你的角色姓名！', 0])
            }
            if ((state.currentCharacter)['角色职业'] === '') {
                warningLog.push(['你需要填写你的角色职业（哪怕只是流浪者）！', 0])
            }
            if ((state.currentCharacter)['角色性别'] === '') {
                warningLog.push(['你需要选择你的角色性别！', 0])
            } else {
                if ((state.currentCharacter)['角色种族'] !== '' && (state.currentCharacter)['角色种族'] !== '其他') {
                    if (!(raceInstructions[(state.currentCharacter)['角色种族']]['性别选择'].includes((state.currentCharacter)['角色性别']))) {
                        warningLog.push(['你选择的种族不支持你选择的性别！', 0])
                    }
                    if ((state.currentCharacter)['角色身高'] < raceInstructions[(state.currentCharacter)['角色种族']]['身高范围'][0]) {
                        warningLog.push(['对于你选择的种族来说，你填写的身高太矮了！', 0])
                    } else if ((state.currentCharacter)['角色身高'] > raceInstructions[(state.currentCharacter)['角色种族']]['身高范围'][1]) {
                        warningLog.push(['对于你选择的种族来说，你填写的身高太高了！', 0])
                    }
                }
            }
            if ((state.currentCharacter)['角色体重'] === 0) {
                warningLog.push(['你填写的体重为0，这只适用于非实体的角色，请再三确认你没有填错。', 1])
            }
            if ((state.currentCharacter)['角色种族'] === '') {
                warningLog.push(['你需要选择你的角色种族！', 0])
            } else if ((state.currentCharacter)['角色种族'] !== '其他') {
                if (['古拉根', '夏默曼', '梅杜萨'].includes((state.currentCharacter)['角色种族'])) {
                    warningLog.push(['你选择了白银纪元种族。这并非是严格禁止的，不过在特定时间线上可能会遭到禁止，请和GM进行协商沟通。', 1])
                } else if (['卡泽尔芙'].includes((state.currentCharacter)['角色种族'])) {
                    warningLog.push(['你选择了黄金纪元种族。这并非是严格禁止的，不过在特定时间线上可能会遭到禁止，请和GM进行协商沟通。', 1])
                }
                ['力量', '体质', '灵巧', '意志', '智慧', '魔力', '魅力', '幸运'].forEach(label => {
                    if ((state.currentCharacter)[label][0] < raceInstructions[(state.currentCharacter)['角色种族']][`${label}范围`][0]) {
                        warningLog.push([`对于你选择的种族来说，你填写的${label}太低了！`, 0])
                    } else if ((state.currentCharacter)[label][0] > raceInstructions[(state.currentCharacter)['角色种族']][`${label}范围`][1]) {
                        warningLog.push([`对于你选择的种族来说，你填写的${label}太高了！`, 0])
                    }
                })
            }
            if ((state.currentCharacter)['角色背景'] === '') {
                warningLog.push(['你没有填写角色背景。这是可以的，但GM强烈建议你填写一个生动的角色背景，那样会使你（和GM）的体验更好。', 1])
            }
            if (state.statePoints > state.maxStatePoints) {
                warningLog.push(['你的总属性点超出了！再降低一些属性值吧！', 0])
            } else if (state.statePoints < state.maxStatePoints - 1) {
                warningLog.push(['你的总属性点并没有被分配完。虽然你可以这样创建角色卡，不过GM还是建议你再分配一些属性。通常来说，所有属性越高越好。', 1])
            }
            ['力量', '体质', '灵巧', '意志', '智慧', '魔力', '魅力', '幸运'].forEach(label => {
                if (state.abilityPoints(label) < state.usedStateAbilityPoints(label)) {
                    warningLog.push([`你的${label}技能点数超出了！少购一些${label}系技能点数吧！`, 0])
                }
            })
            if (state.generalStatePoint < state.usedStateAbilityPoints('通用')) {
                warningLog.push(['你的通用技能点数超出了！少购一些技能点数吧！', 0])
            } else if (state.generalStatePoint - 1000 > state.usedStateAbilityPoints('通用')) {
                warningLog.push(['你的技能点数还余有超过1000点。也许你是为了之后的计划保留着它们，请再三确认你没有填错。', 1])
            }
            if (state.comprehensionPoints > state.level * 3 + 5 + (state.currentCharacter)['领悟力']) {
                warningLog.push(['你的领悟力点数超出了！想想办法！要么清空一些高阶技能点数，要么在天赋上克扣吧！', 0])
            } else if (state.comprehensionPoints < state.level * 3 + 5 + (state.currentCharacter)['领悟力'] - 20) {
                warningLog.push(['你的领悟力还余有超过20点。通常来说这不是什么好主意，不过也许你是为了之后的计划保留着它们，请再三确认你没有填错。', 1])
            }
            if (warningLog.length === 0) {
                warningLog.push(['恭喜！你的角色卡看上去很不错！', 2])
            }
            return warningLog
        },
        characterBuilderDownloadable: (state) => {
            const validationResult = state.characterBuilderValidation
            let downloadable = true
            for (let i = 0; i < validationResult.length; i++) {
                if (validationResult[i][1] === 0) {
                    downloadable = false
                    break
                }
            }
            return downloadable
        },
    },
    actions: {
        setAlert(title, content) {
            this.windowAlert = true
            this.windowAlertTitle = title
            this.windowAlertContent = content
        },
        setCharacter(character) {
            const previousVersion = character['版本']
            try {
                const vi = getVersionInitialisation()
                const newCharacter = {}
                Object.entries(vi).forEach(([key, value]) => {
                    if (key === '版本') {
                        newCharacter[key] = value
                    } else {
                        newCharacter[key] = character[key] ?? value
                    }
                })
                this.currentCharacter = newCharacter
                const thisVersion = newCharacter['版本']
                if (previousVersion) {
                    if (previousVersion === thisVersion) {
                        this.setAlert('角色信息导入完毕', ['现在你可以正常使用角色卡构筑器了！'])
                    } else {
                        this.setAlert('角色信息更新完毕', [`原版本：v${previousVersion}`, `现版本：v${thisVersion}`,
                            '你的角色数据已经被更新到了最新版！',
                            '现在你可以正常使用角色卡构筑器了！'])
                    }
                }
            } catch {
                this.setAlert('角色信息导入错误！', ['你导入的角色信息文件必须为JSON文件！'])
            }
        },
        saveCharacter() {
            try {
                const jsonStr = JSON.stringify(this.currentCharacter, null, 2)
                const blob = new Blob([jsonStr], { type: "application/json" })
                const url = URL.createObjectURL(blob)
                const a = document.createElement("a")
                a.href = url
                a.download = `CGC角色卡-${(this.currentCharacter)['角色名字']}(v${(this.currentCharacter)['版本']}).json`
                document.body.appendChild(a)
                a.click()
                document.body.removeChild(a)
                URL.revokeObjectURL(url)
            } catch {
                this.setAlert('角色信息保存错误', ['保存角色卡发生错误！', '请找Amo解决！'])
            }
        }
    },
})